import React from 'react';
import Aux from '../../../hoc/_Aux/_Aux';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const productTesting = props => {
	return (
		<Aux>
			<Helmet>
				<title>UX Pt. 5: Product Validation</title>
				<meta content="Dan Beltramo - About Me" name="description" />
			</Helmet>
			<div className="l-w1 l-flexbox-alldevs projTitle">
				<div className="introList animated delay3">
					<li className="abcd typ-surface-color typ-light-color">Part 5</li>
						</div>
					</div>
			<div className="l-w1 m-projCopy animated">
			<h1 className="m-quote starter m-t-20">Product Validation</h1>
			</div>

			<div className="l-w1 process_bottom">
				<p className="m-projCopy">
					This phase reveals users pain points and gaps in user flows; it is important to test the validity
					of all perceptions from designers and stakeholders. After the testing sessions, UX researchers will
					analyse the findings thoroughly and will provide a full range of design solutions.
					</p></div>

					<div className="l-flexbox-grey l-w1">
						<div className="m-twoColumnCopy process_bottom">
							<h2 className="m-projTitleGroup typ-lighttext-color">Task analysis</h2>
							<p className="m-projCopy">
							Tasks are pre-determined, and a researcher will observe as a user is completing those tasks and ask
							questions to better understand pain points. This testing often reveals specific pain points as well
							as gaps in user flows.
							</p>
						</div>
						<div className="m-twoColumnCopy process_bottom">
							<h2 className="m-projTitleGroup typ-lighttext-color">A/B testing</h2>
							<p className="m-projCopy">a controlled experiment to compare versions of a page or flow
							in order to optimize a single page (ex. increasing the number of sign ups by changing the color of a
							button in a website).</p>
						</div>
					</div>
					<div className="l-flexbox-grey l-w1">
						<div className="m-twoColumnCopy process_bottom">
							<h2 className="m-projTitleGroup typ-lighttext-color">The SUS rating scale</h2>
							<p className="m-projCopy">
							a questionnaire that the user completes to give an overall satisfaction score, and is a typical measures of usability (the official measure of user
							satisfaction).
							</p>
						</div>
						<div className="m-twoColumnCopy  process_bottom">
						<h2 className="m-projTitleGroup typ-lighttext-color">WCAG</h2>
						<p className="m-projCopy">
						Web Content Accessibility Guidelines
						</p>
</div></div>

			<div className="l-w0 m-mobileProjLink">
							<Link to="/">
							<button className="projectDetails no-background typ-surface-color m-b-60">
								<span className="button-text typ-light-color">Back Home</span> <span className="material-icons typ-light-color">arrow_forward</span>
							</button>
							</Link>
						</div>

		</Aux>
	);
};

export default productTesting;
