import React from 'react';
import Aux from '../../../hoc/_Aux/_Aux';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const analysis = props => {
  return (
  <Aux>
  <Helmet>
  <title>UX Pt. 3: UX Product Analysis</title>
  <meta content="Dan Beltramo - About Me" name="description" />
  </Helmet>
  <div className="l-w1 projTitle">
  <div className="introList animated delay3">
  <li className="abcd typ-surface-color typ-light-color">Part 3</li>
  </div>
  </div>
  <div className="l-w1 m-projCopy animated">
<h1 className="m-quote starter m-t-20">Design Development</h1>
  </div>
  <div className="l-flexbox-grey l-w1">
<div className="m-twoColumnCopy process_bottom">
  <h2 className="m-projTitleGroup typ-lighttext-color">User Flows</h2>
  <p className="m-projCopy">
  A user flow is a flowchart of a users step-by-step experience and decisionmaking through a product or feature. User flows can also depict triggers, ex. emails, text messages, or alerts fired off. Reviewing user flow visuals allows quick alignment and evaluate and optimization of the experience by the team. A related methodology developed by Signal v. Noise allows for even easier outlining of complex user flows, where each moment in a flow is labeled above a dashed line, with the user's potential actions listed below it. Arrows are then drawn to connect a user's potential action with a new page or content.
  </p>
</div>
</div>
  <div className="l-flexbox-grey l-w1">
  <div className="m-twoColumnCopy process_bottom">
  <h2 className="m-projTitleGroup typ-lighttext-color">Mockups</h2>
  <p className="m-projCopy">
  Mockups include details of user flow and interface, with selected colors, type styles, samples of real data and content, etc. Mockups look more like the final product, but do not represent the interactions yet. These are used to validate decisions and better understand user needs before development begins.
  </p>
  </div>

  <div className="m-twoColumnCopy process_bottom">
  <h2 className="m-projTitleGroup typ-lighttext-color">Prototypes</h2>
  <p className="m-projCopy">
  Prototypes are early interactive groups of mockups, useful for quickly honing a product's scope and direction, as well as collecting feedback. Prototypes are cost effective,  as they're a much more resource efficient method of visualizing options, compared to having options being built in code. Prototypes can build on insights from the User/Product Research phases, and either reinforce or disregard previous assumptions.
  </p>
</div>
</div>
  <div className="l-w0 m-mobileProjLink">
  <Link to="/ux/design">
  <button className="projectDetails no-background typ-surface-color m-b-60">
  <span className="button-text typ-light-color">UX Design</span> <span className="material-icons typ-light-color">arrow_forward</span>
  </button>
  </Link>
  </div>


  </Aux>
  );
};

export default analysis;
