import React from 'react';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

import Aux from '../../hoc/_Aux/_Aux';
import resumePath from '../../assets/images/DanBeltramo_Resume_2023.pdf';

const about = props => {
	return (
		<Aux>
			<Helmet>
				<title>About</title>
				<meta name="description" content="Dan Beltramo - About Me" />
			</Helmet>
<div className="home-background-color">
						<div className="animated introList l-w1 delay">
			<h1 className="pullquote home-light-color m-t-20 about-text">
	Hi! I'm a UX/UI designer based in my hometown of Portland, Oregon, and am currently a Visual Designer on the Google Pixel team. With over 9 years experience, I am well versed at translating complex needs to intuitive experiences through balanced user-centered end-to-end design.
			<br/><br/>
			Coming from the Architecture profession, I transitioned into UX in pursuit of responsive design and intuitive experiences. There are many parallels between UX design and architectural design, and my dedication to delightful and inclusive, functionally affective and long-lasting design is a driving force in my career.
			 <br/><br/>
			I am an enthusiastic and collaborative designer, and find joy in working with others to create amazing products. My strengths include visual design and a sharp eye for detail. From my perspective, good design requires restraint. Thorough assessment of what can be removed to enhance a products value rather than deplete it. It's what you leave out that counts.
</h1>
<div class="resume">
					<a href={resumePath} target="_blank" rel="noopener noreferrer">
					<button className="m-blackText m-b-60 home-surface-color home-special-color">
						Resume
					</button>
					</a>
				</div>
			</div>
</div>
		</Aux>
	);
};

export default about;
