import React from 'react';
import Aux from '../../../hoc/_Aux/_Aux';
import { Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';

const productDefinition = props => {
  return (
  <Aux>
  <Helmet>
  <title>UX Pt. 1: Defining the Product</title>
  <meta content="Dan Beltramo - About Me" name="description" />
  </Helmet>
  <div className="l-w1 l-flexbox-alldevs projTitle">
  <div className="introList animated delay3">
  <li className="abcd typ-surface-color typ-light-color">Part 1</li>
  </div>
  </div>
  <div className="l-w1 m-projCopy animated">
  <h1 className="m-quote starter m-t-20">Empathize</h1>
  </div>

  <section class="l-w1 process_bottom">
  <p class="m-projCopy">
  Empathizing begins with answering a number of questions, like what the product is, who its for, and how it will be used.  As well, it's recommended to pin down goals and values through stakeholder interviews in this initial phase. As this phase wraps up, a project kickoff meeting is recommended to define the products scope, identify teams, and set expectations for the project.
  </p>
  </section>

  <div className="l-w0 m-mobileProjLink">
  <Link to="/ux/userResearch">
  <button className="projectDetails no-background typ-surface-color m-b-60">
  <span className="button-text typ-light-color">User Research</span> <span className="material-icons typ-light-color">arrow_forward</span>
  </button>
  </Link>
  </div>



  </Aux>
  );
};

export default productDefinition;
